}

.backgreen{
    background-color:#304945;
}
.shadows{
    -webkit-filter: drop-shadow(20px 20px 10px black);
            filter: drop-shadow(20px 20px 10px black);
}

.fadeinRight{
    -webkit-animation: fadeinRight 1.6s both;
            animation: fadeinRight 1.6s both;
}
@-webkit-keyframes fadeinRight{
    0%{
     opacity:0;
     -webkit-transform:translateX(50px);
             transform:translateX(50px)
    }100%{
     opacity:1;
     -webkit-transform:translateX(0);
             transform:translateX(0)
    }
}
@keyframes fadeinRight{
    0%{
     opacity:0;
     -webkit-transform:translateX(50px);
             transform:translateX(50px)
    }100%{
     opacity:1;
     -webkit-transform:translateX(0);
             transform:translateX(0)
    }
}
.fadeinLeft{
    -webkit-animation: fadeinLeft 1.6s both;
            animation: fadeinLeft 1.6s both;
}
@-webkit-keyframes fadeinLeft{
    0%{
     opacity:0;
     -webkit-transform:translateX(-50px);
             transform:translateX(-50px)
    }100%{
     opacity:1;
     -webkit-transform:translateX(0);
             transform:translateX(0)
    }
}
@keyframes fadeinLeft{
    0%{
     opacity:0;
     -webkit-transform:translateX(-50px);
             transform:translateX(-50px)
    }100%{
     opacity:1;
     -webkit-transform:translateX(0);
             transform:translateX(0)
    }
}





